/* eslint-disable react/jsx-one-expression-per-line */
import React from 'react';
import uuid from 'uuid';
import './style.css';

export default function index({ boxes }) {
  return (
    <div className="top--about">
      <div className="container">
        <h2>Reasons To Choose</h2>
        <p>
          A successful company generates wealth sustainably and continuously for
          its shareholders, employees and the community in which it operates. We
          support entrepreneurs in the realization of their company’s potential.
        </p>
        <div className="row">
          {boxes.map(box => (
            <div key={uuid()} className="col-lg-3 col-md-4 col-sm-6 col-12">
              <div className="box--about">
                <i className={`${box.icon} fa-2x`} />
                <h6>{box.title}</h6>
                <p>{box.desc}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
