import React, { Component } from 'react';
import Header from '../../common/Header';
import Breadcrumb from '../../common/breadcrumb';
import Body from './Body';
import Footer from '../../common/Footer';
import CopyRights from '../../common/CopyRights';
import Loading from '../../common/Loading';

export default class index extends Component {
    state = {
      boxes: [
        {
          icon: 'fas fa-book-open',
          title: 'Confidentiality',
          desc: 'Confidentiality is one of the most important aspects of our work. We protect the privacy',
        },
        {
          icon: 'fas fa-book-open',
          title: 'Confidentiality',
          desc: 'Confidentiality is one of the most important aspects of our work. We protect the privacy',
        },
        {
          icon: 'fas fa-book-open',
          title: 'Confidentiality',
          desc: 'Confidentiality is one of the most important aspects of our work. We protect the privacy',
        },
        {
          icon: 'fas fa-book-open',
          title: 'Confidentiality',
          desc: 'Confidentiality is one of the most important aspects of our work. We protect the privacy',
        },
      ],
      statistics: [
        {
          number: '50',
          title: 'Employees',
        },
        {
          number: '4',
          title: 'offices',
        },
        {
          number: '15',
          title: 'Employees',
        },
        {
          number: '77',
          title: 'websites',
        },
        {
          number: '50',
          title: 'Employees',
        },
      ],
      background: 'https://blogs.forbes.com/glennllopis/files/2018/02/shutterstock_1253381451.jpg',
      loading: false,
    }


    componentWillMount() {
      this.setState({ loading: true });
      const intervalId = setInterval(this.switchLoading(), 1000);
      setTimeout(() => {
        this.setState({ loading: false }, () => {
          clearInterval(intervalId);
        });
      }, 2000);
    }

    switchLoading = () => {
      this.setState({ loading: !this.state.loading });
    }


    render() {
      const {
        boxes, statistics, background, loading,
      } = this.state;
      return (
        <>
          {loading ? <Loading /> : null}
          <Header />
          <div className="container">
            <Breadcrumb data="About us" />
          </div>
          <Body
            boxes={boxes}
            statistics={statistics}
            background={background}
          />
          <Footer />
          <CopyRights />
        </>
      );
    }
}
