import React from 'react';
import { Link } from 'react-router-dom';
import ReactHtmlParser from 'react-html-parser';
import './style.css';

export default function index({ postDetails }) {
  const { details } = postDetails;
  return (
    <div className="post--details">
      {details ? ReactHtmlParser(details) : null}
      <video controls track="dd">
        <source
          src="https://int.nyt.com/data/videotape/finished/2018/02/1519674915/rooseloop4-650w.mp4"
          type="video/mp4"
        />
        Your browser does not support HTML5 video.
      </video>
      <p>
        Lorem ipsum, dolor sit amet consectetur adipisicing elit. Optio vel
        cupiditate perferendis alias!
        <Link to="/"> This is google</Link>
      </p>
      <p>
        Lorem ipsum, dolor sit amet consectetur adipisicing elit. Optio vel
        cupiditate perferendis alias! Fugit nobis expedita consequuntur ipsam
        ullam possimus, molestiae dolore earum. Eligendi, mollitia debitis
        sapiente qui accusantium sequi!
      </p>
    </div>
  );
}
