/* eslint-disable react/prop-types */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import FirstFotter from './FirstFooter';
import SecondFooter from './SecondFooter';
import './style.css';

class index extends Component {
  state = {
    first:
      'We decided to use this room, on the second floor and overlookingWe decided to use this room, on the second floor and overlookingWe decided to use this room, on the second floor and overlooking',
    mobile: '+972597116335',
    email: 'mohannad.hanafi96@gmail.com',
  };

  goToHome = () => {
    const { history } = this.props;
    history.push('/');
  }

  render() {
    const { first, mobile, email } = this.state;
    return (
      <div className="footer">
        <div className="container">
          <div className="footer--container">
            <FirstFotter
              first={first}
              mobile={mobile}
              email={email}
              onClick={this.goToHome}
            />
            <SecondFooter />
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(index);
