/* eslint-disable react/prop-types */
import React from 'react';
import uuid from 'uuid';
import './style.css';

export default function index({ users, user, newComment }) {
  return (
    <div className="comments">
      {newComment.map(element => (
        <div key={uuid()} className="single--comment">
          <img src={users ? `/storage/${users.photo_link}` : null} alt="profile" />
          <div className="comment--content">
            <h5>{user ? user.name : null}</h5>
            <span>{users ? users.created_at : null}</span>
            <p>{element}</p>
          </div>
        </div>
      ))}
    </div>
  );
}
