import React from 'react';
import './style.css';

export default function index({ details }) {
  return (
    <div className="auther--details">
      <img
        src={details ? `/storage/${details.photo_link}` : null}
        alt="profile"
      />
      <div>
        <h5>Written By</h5>
        <h4>{details ? details.name : null}</h4>
        <p>
          Lorem ipsum dolor sit, amet consectetur adipisicing elit. Saepe
          ducimus sunt minus nulla! Eum distinctio excepturi, nesciunt sapiente
          iusto reiciendis fugiat. Deleniti, ratione adipisci quae sunt
          distinctio perferendis libero beatae.
        </p>
      </div>
    </div>
  );
}

// `/storage/${auther.photo_link}`
