/* eslint-disable react/jsx-one-expression-per-line */
import React from 'react';
import uuid from 'uuid';
import './style.css';

export default function index({ statistics, background }) {
  return (
    <div className="bottom--about">
      <div className="container">
        <h2>Our Story</h2>
        <p>
          In 2006, founders Boris and Patrick were looking for a tech event to
          showcase their new startup. When they couldn’t find one that checked
          all the boxes they decided to host their own. As they were organizing
          things, they soon realized they needed somewhere to promote the
          conference, and that’s how thenextweb.com was launched. It later
          became a blog and the result is what you are looking at right now.
        </p>
      </div>
      <div
        className="about--bg"
        style={{
          backgroundImage:
            `url(${background})`,
        }}
      >
        <div className="about--bg--overlay">
          <div className="container">
            <div className="content">
              {statistics.map(statistic => (
                <div key={uuid()}>
                  <h3>{statistic.number}</h3>
                  <h6>{statistic.title}</h6>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
