import React, { Component } from 'react';
import Header from '../../common/Header';
import Footer from '../../common/Footer';
import Copyrights from '../../common/CopyRights';
import Breadcrumb from '../../common/breadcrumb';
import Body from './Body';
import Loading from '../../common/Loading';

export default class index extends Component {
  state = {
    name: '',
    email: '',
    subject: '',
    message: '',
    social: [
      { image: 'https://i.ibb.co/D9Xzh8k/facebook.png', number: 25151 },
      { image: 'https://i.ibb.co/5xzNkx9/insta.png', number: 26448 },
      { image: 'https://i.ibb.co/1b4RZ3t/snapchat.png', number: 5442 },
      { image: 'https://i.ibb.co/mGLYcfv/soundcloud.png', number: 3131 },
      { image: 'https://i.ibb.co/bFjNJj0/twitter.png', number: 1563 },
      { image: 'https://i.ibb.co/fpDLfcr/youtube.png', number: 5485 },
    ],
    loading: false,
  }

  componentWillMount() {
    this.setState({ loading: true });
    const intervalId = setInterval(this.switchLoading(), 1000);
    setTimeout(() => {
      this.setState({ loading: false }, () => {
        clearInterval(intervalId);
      });
    }, 2000);
  }

  switchLoading = () => {
    this.setState({ loading: !this.state.loading });
  }


  onChange = ({ target: { name, value } }) => {
    this.setState({ [name]: value });
  }

  render() {
    const { social, loading } = this.state;
    return (
      <>
        {loading ? <Loading /> : null}
        <Header />
        <div className="container">
          <Breadcrumb data="Contact" />
          <Body
            onChange={this.onChange}
            form={this.state}
            social={social}
          />
        </div>
        {/* <Footer />
        <Copyrights /> */}
      </>
    );
  }
}
