/* eslint-disable camelcase */
/* eslint-disable react/prop-types */
import React, { Component } from 'react';
import uuid from 'uuid';
import axios from 'axios';
import Fade from 'react-reveal/Fade';
import { Link, withRouter } from 'react-router-dom';
import Heading from '../../../../../common/Heading';
import Title from '../../../../../common/Title';
import './style.css';

class LeftFourth extends Component {
  state = { loading: true, news: [] };

  componentDidMount() {
    const { sectionSeoName } = this.props;
    axios(`/api/v1/CatWithPosts/${sectionSeoName}`)
      .then((result) => {
        const {
          data,
        } = result;
        const { category_name } = data[0];
        this.setState(() => ({
          news: data,
          title: category_name,
        }));
      })
      .catch(() => {});
  }

  render() {
    const { news, title } = this.state;
    const posts = news;
    const topNews = posts && posts.slice(0, 2);
    const bottomNews = posts && posts.slice(2, 8);
    return (
      <Fade big>
        <div className="left-fourth">
          <Heading desc={title || ''} />
          <div className="left-fourth-top">
            {topNews.length
              ? topNews.map(element => (
                <div
                  key={uuid()}
                  className="top-news"
                  style={{
                    backgroundImage: `url(/api/v1/getFile/${
                      element.header_media
                    }`,
                  }}
                >
                  <div className="top-news-details">
                    <Title title={element.category_name} />
                    <Link
                      key={uuid()}
                      to={`/news/${element.category_seo}/${
                        element.seo_name
                      }`}
                    >
                      {element.title}
                    </Link>
                    <span>16/01/1996</span>
                  </div>
                </div>
              ))
              : null}
          </div>
          <div className="left-fourth-bottom">
            {bottomNews.length
              ? bottomNews.map(element => (
                <div key={uuid()} className="fourth-element">
                  <div
                    className="news-img"
                    style={{
                      backgroundImage: `url(/api/v1/getFile/${
                        element.header_media
                      })`,
                    }}
                    alt="main-news "
                  />
                  <div>
                    <Link
                      key={uuid()}
                      to={`/news/${element.category_seo}/${
                        element.seo
                      }`}
                    >
                      {element.title}
                    </Link>
                    <span>16/01/1996</span>
                  </div>
                </div>
              ))
              : null}
          </div>
        </div>
      </Fade>
    );
  }
}

export default withRouter(LeftFourth);
