/* eslint-disable react/prop-types */
import React, { Component } from 'react';
import axios from 'axios';
import Header from '../../common/Header';
import Footer from '../../common/Footer';
import CoypRights from '../../common/CopyRights';
import Loading from '../../common/Loading';

import CategotyContainer from './Container';
import './style.css';

export default class index extends Component {
    state = { news: '', loading: false }


    componentDidMount() {
      const { match: { params: { seoName } } } = this.props;
      this.setState({ loading: true });
      const intervalId = setInterval(this.switchLoading(), 1000);
      setTimeout(() => {
        this.setState({ loading: false }, () => {
          clearInterval(intervalId);
        });
      }, 1000);
      this.getData(seoName);
    }

    componentWillUpdate(prevProps) {
      const { match: { params: { seoName: newSeoName } } } = prevProps;
      const { match: { params: { seoName } } } = this.props;
      if (newSeoName !== seoName) {
        this.getData(newSeoName);
      }
    }

    getData = (seoName) => {
      this.switchLoading();
      const intervalId = setInterval(this.switchLoading(), 1000);
      setTimeout(() => {
        this.setState({ loading: false }, () => {
          clearInterval(intervalId);
        });
      }, 1000);
      axios(`/api/getCategoryPosts?seo_name=${seoName}&per_page=7`)
        .then((result) => {
          const { data } = result.data;
          this.setState(() => ({
            news: data,
          }));
        })
        .catch((error) => {
          const { response: { status } } = error;
          // console.log(error.response.status);
          if (status === 403) {
            this.props.history.push('/error');
          }
        });
    }

    switchLoading = () => {
      this.setState({ loading: !this.state.loading });
    }

    render() {
      const { news, loading } = this.state;
      return (
        <React.Fragment>
          {loading ? <Loading /> : null}
          <Header />
          <CategotyContainer switchLoading={this.switchLoading} loading={loading} news={news || ''} />
          <Footer />
          <CoypRights />
        </React.Fragment>
      );
    }
}
