/* eslint-disable camelcase */
/* eslint-disable react/jsx-one-expression-per-line */
/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import React from 'react';
import './style.css';

export default function index(props) {
  const { postDetails } = props;
  const {
    header_photo_link, name, auther, category,
  } = postDetails;
  return (
    <div className="article">
      <img
        src={header_photo_link ? `/storage/${header_photo_link}` : null}
        alt="article"
      />
      <h2>{name}</h2>
      <div className="info">
        <div className="user">
          <img
            src={auther ? `/storage/${auther.photo_link}` : null}
            alt="pro"
          />
          <h5>
            by <span>{auther ? auther.name : null}</span> 1 day ago in{' '}
            <span>{category ? category.name : null}</span>
          </h5>
        </div>
        <div className="comments">
          <div>
            <i className="fas fa-comment" />
            <h6>10 comments</h6>
          </div>
          <i className="fas fa-print" />
        </div>
      </div>
      <div className="social--post">
        <h4>
          100 <span>shares</span>
        </h4>
        <div className="social--icons">
          <div className="facebook">
            <img src="https://i.ibb.co/0Fpmjxd/Shape-3.png" alt="facebook" />
            <span>Share On Facebook</span>
          </div>
          <div className="twitter">
            <img
              className="twitter"
              src="https://i.ibb.co/cNJzvH1/Shape-4.png"
              alt="twitter"
            />
            <span>Tweet</span>
          </div>
          <img
            src="https://i.ibb.co/YbvXz3J/Vector-Smart-Object1.png"
            alt="whatsapp"
          />
          <img
            src="https://i.ibb.co/vLmQ0QL/Vector-Smart-Object.png"
            alt="linkedin"
          />
        </div>
        <div className="share">
          <i className="fas fa-share-alt" />
          <p>http://medse.co/255x3</p>
        </div>
      </div>
    </div>
  );
}
