/* eslint-disable camelcase */
/* eslint-disable react/prop-types */
import React, { Component, Fragment } from 'react';
import Slider from 'react-slick';
import uuid from 'uuid';
import axios from 'axios';
import RemoveTag from 'striptags';
import Fade from 'react-reveal/Fade';
import { Link, withRouter } from 'react-router-dom';
import Arrow from './Arrow';
import Heading from '../../../../../common/Heading';
import Title from '../../../../../common/Title';

import './style.css';

class ThirdFifth extends Component {
  state = {
    settings: {
      prevArrow: <Arrow />,
      nextArrow: <Arrow />,
      infinite: true,
      speed: 1000,
      slidesToShow: 1,
      slidesToScroll: 1,
      autoplay: true,
      autoplaySpeed: 4000,
    },
    loading: false,
    news: [],
  };

  componentDidMount() {
    const { sectionSeoName } = this.props;
    axios(`/api/v1/CatWithPosts/${sectionSeoName}`)
      .then((result) => {
        const {
          data,
        } = result;
        const { category_name } = data[0];
        this.setState(() => ({
          news: data,
          title: category_name,
        }));
      })
      .catch(() => {});
  }

  render() {
    const {
      title, settings, news,
    } = this.state;
    const posts = news.slice(0, 5);
    return (
      <Fragment>
        <div className="third-fifth">
          <Heading desc={title || ''} />
          <Slider {...settings}>
            {posts.length ? posts.map(element => (
              <Fragment key={uuid()}>
                <Fade>
                  <div>
                    <div
                      className="fifth-image-slider"
                      style={{ backgroundImage: `url(/api/v1/getFile/${element.header_media})` }}
                    >
                      <div className="fifth-slider-overlay">
                        <div className="container">
                          <Title title={element.category_name} />
                          <Link to={`/news/${title}/${element.seo}`}>
                            <span className="news">
                              {element.title}
                            </span>
                          </Link>
                          <span className="news-details">{RemoveTag(element.description)}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </Fade>
              </Fragment>
            )) : null}
          </Slider>
        </div>
      </Fragment>
    );
  }
}

export default withRouter(ThirdFifth);
