import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import uuid from 'uuid';
import './style.css';

export default class index extends Component {
  state = {
    date: 'Tuesday, 5 FEB, 2019',
    nav: [
      {
        desc: 'About us',
        link: '/aboutus',
      },
      {
        desc: 'Contact',
        link: '/contact',
      },
      {
        desc: 'Archieves',
        link: '/',
      },
    ],
    social: [
      'fab fa-facebook-f',
      'fab fa-twitter',
      'fab fa-whatsapp',
      'fab fa-google-plus-g',
    ],
  }

  render() {
    const { date, nav, social } = this.state;
    return (
      <div className="first--nav">
        <div className="container">
          <div className="first--nav--data">
            <div className="first--left">
              <span>{date}</span>
              {nav.map(element => (
                <span key={uuid()}><Link to={element.link}>{element.desc}</Link></span>
              ))}
            </div>
            <div className="first--right">
              {social.map(element => (
                <i key={uuid()} className={element} />
              ))}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
