/* eslint-disable camelcase */
/* eslint-disable react/prop-types */
import React, { Component, Fragment } from 'react';
import uuid from 'uuid';
import './style.css';
import axios from 'axios';
import Fade from 'react-reveal/Fade';
import { Link, withRouter } from 'react-router-dom';

import Heading from '../../../../../common/Heading';

class LeftSixth extends Component {
  state = {
    news: [],
  };

  componentDidMount() {
    const { sectionSeoName } = this.props;
    axios(`/api/v1/CatWithPosts/${sectionSeoName}`)
      .then((result) => {
        const {
          data,
        } = result;
        const { category_name } = data[0];
        this.setState(() => ({
          news: data,
          title: category_name,
        }));
      })
      .catch(() => {});
  }

  render() {
    const { news, title } = this.state;
    const posts = news.slice(0, 6);
    return (
      <div className="left-sixth">
        <Heading desc={title || ''} />
        <div className="left-sixth-container">
          {posts.length
            ? posts.map(element => (
              <Fragment key={uuid()}>
                <Fade>
                  <div className="sixth-element">
                    <div
                      className="news-img"
                      style={{
                        backgroundImage: `url(/api/v1/getFile/${
                          element.header_media
                        })`,
                      }}
                      alt="main-news "
                    />
                    <div>
                      <Link to={`/news/${element.category_seo}/${element.seo}`}>
                        {element.title}
                      </Link>
                      <span>16/01/1996</span>
                    </div>
                  </div>
                </Fade>
              </Fragment>
            ))
            : null}
        </div>
      </div>
    );
  }
}

export default withRouter(LeftSixth);
