/* eslint-disable camelcase */
/* eslint-disable react/prop-types */
import React, { Component, Fragment } from 'react';
import uuid from 'uuid';
import axios from 'axios';
import { Link, withRouter } from 'react-router-dom';
import RemoveTags from 'striptags';
import Fade from 'react-reveal/Fade';
import Heading from '../../../../../common/Heading';
import Title from '../../../../../common/Title';

import './style.css';

class LeftFirst extends Component {
  state = {
    news: [],
  };

  componentDidMount() {
    const { sectionSeoName } = this.props;
    axios(`/api/v1/CatWithPosts/${sectionSeoName}`)
      .then((result) => {
        const {
          data,
        } = result;
        const { category_name } = data[0];
        this.setState(() => ({
          news: data,
          title: category_name,
        }));
      })
      .catch(() => {});
  }

  render() {
    const { news, title } = this.state;
    const posts = news && news.reverse();
    const otherNews = posts && posts.slice(1, 5);
    return (
      <React.Fragment>
        <Heading desc={title || ''} />
        <div className="left-first">
          {posts.length ? (
            <Fade>
              <div className="main-news">
                <div
                  className="main-news-img"
                  style={{
                    backgroundImage: `url(/api/v1/getFile/${posts
                      && posts[0].header_media})`,
                  }}
                  alt="main-news "
                />
                <div>
                  <Title title={posts[0].category_name} />
                </div>
                <Link
                  to={`/news/${posts[0].category_seo}/${
                    posts[0].seo
                  }`}
                >
                  {posts[0].title}
                </Link>
                <p>{RemoveTags(posts[0].description)}</p>
                <span>16/01/2019</span>
              </div>
            </Fade>
          ) : null}

          <div className="other-news">
            {otherNews.length
              ? otherNews.map(element => (
                <Fragment key={uuid()}>
                  <Fade>
                    <div className="other-news-element">
                      <div
                        className="news-img"
                        style={{
                          backgroundImage: `url(/api/v1/getFile/${
                            element.header_media[0]
                          })`,
                        }}
                        alt="main-news "
                      />
                      <div>
                        <Link
                          key={uuid()}
                          to={`/news/${title}/${element.seo}`}
                        >
                          {RemoveTags(element.description)}
                        </Link>
                        <span>16/01/1996</span>
                      </div>
                    </div>
                  </Fade>
                </Fragment>
              ))
              : null}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default withRouter(LeftFirst);
