/* eslint-disable react/prop-types */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import './style.css';


class index extends Component {
state = {}

onClick = (catName, seoName) => {
  this.props.history.push(`/news/${catName}/${seoName}`);
}

render() {
  const { previous, next } = this.props.data;

  return (
    previous || next ? (
      <div className="next--posts">
        <div>
          <h5>Previous</h5>
          {previous ? (
            <span onClick={() => this.onClick(previous.category.name, previous.seo_name)}>{previous.name}</span>
          ) : (
            <span className="disable">This is the first one</span>
          )}
        </div>
        <div>
          <h5>Next</h5>
          {next ? (
            <span onClick={() => this.onClick(next.category.name, next.seo_name)}>{next.name}</span>
          ) : (
            <span className="disable">This is the last one</span>
          )}
        </div>
      </div>
    ) : null
  );
}
}

export default withRouter(index);
