/* eslint-disable camelcase */
/* eslint-disable react/prop-types */
import React, { Fragment, Component } from 'react';
import uuid from 'uuid';
import { Link } from 'react-router-dom';
import removeTags from 'striptags';
import axios from 'axios';
import BreadCrumb from '../../../../common/breadcrumb';
import Title from '../../../../common/Title';
import './style.css';

export default class TopSection extends Component {
  state = {
    photoLink: '', details: '', title: '', loading: false, data: '',
  }

  componentDidMount() {
    axios('/api/getAds').then((result) => {
      const { data: { data: { data } } } = result;
      const { photo_link: photoLink, details, title } = data[0];
      this.setState({ photoLink, details, title });
    });
  }


  static getDerivedStateFromProps(nextProps) {
    const { data } = nextProps;

    return { data };
  }


  render() {
    const { photoLink, title } = this.state;
    const { data } = this.state;

    const {
      posts: { data: finalResult },
      name,
    } = data;
    const finalPosts = finalResult;
    const first = finalPosts.slice(0, 1);
    const second = finalPosts.slice(1, 5);
    return (
      <>
        <div className="top--section">
          <BreadCrumb data={data.name} />
          <div className="last--posts">
            {finalPosts.length
              ? (
                <>
                  <div className="first--photo">
                    <img src={`/storage/${first[0].header_photo_link}`} alt="post" />
                    <div className="photo--data">
                      <Title title={first[0].category.name} />
                      <p>
                        <Link to={`/news/${first[0].category.name}/${first[0].seo_name}`}>
                          {removeTags(first[0].name)}
                        </Link>
                      </p>
                    </div>
                    <div className="gradient" />
                  </div>
                  <div className="second--photos">
                    {second.map(post => (
                      <Fragment key={uuid()}>
                        <div className="container--images">
                          <img
                            src={`/storage/${post.header_photo_link}`}
                            alt="post"
                          />
                          <div className="photo--data">
                            <Title title={post.category.name} />
                            <p>
                              <Link to={`/news/${post.category.name}/${post.seo_name}`}>
                                {removeTags(post.name)}
                              </Link>
                            </p>
                          </div>
                          <div className="gradient" />
                        </div>
                      </Fragment>
                    ))}
                  </div>
                </>
              )
              : <h1>category is Empty</h1> }
          </div>
          <img className="adv" src={`/storage/${photoLink}`} alt={title} />
        </div>
      </>
    );
  }
}
