import React, { Component, Fragment } from 'react';
import FirstNav from './FirstNav';
import SecondNav from './SecondNav';
import ThirdNav from './ThirdNav';

export default class index extends Component {
  state = {};

componentDidMount = () => {
  window.scrollTo(0, 0);
}

render() {
  return (
    <Fragment>
      <FirstNav />
      <SecondNav />
      <ThirdNav />
    </Fragment>
  );
}
}
