/* eslint-disable react/prop-types */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import axios from 'axios';
import RightSection from '../../../common/RightSection';
import Article from './Article';
import ArticleDetails from './ArticleDetails';
import ArticleTags from './ArticleTags';
import Auther from './Auther';
import Next from './Next';
import Heading from '../../../common/Heading';
import Comment from './Comment';
import Loading from '../../../common/Loading';
import Comments from './Comments';
import './style.css';

class NewsPageContainer extends Component {
  state = {
    right: {
      image:
        'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
      heading1: 'Keep In Touch',
      video:
        'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
      heading2: 'Videos',
      heading3: 'Popular Posts',
      posts: [
        {
          image:
            'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
          title:
            'We decided to use this room, on the second floor and overlooking',
          date: 'Jan 23, 2018',
        },
        {
          image:
            'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
          title:
            'We decided to use this room, on the second floor and overlooking',
          date: 'Jan 23, 2018',
        },
        {
          image:
            'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
          title:
            'We decided to use this room, on the second floor and overlooking',
          date: 'Jan 23, 2018',
        },
        {
          image:
            'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
          title:
            'We decided to use this room, on the second floor and overlooking',
          date: 'Jan 23, 2018',
        },
        {
          image:
            'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
          title:
            'We decided to use this room, on the second floor and overlooking',
          date: 'Jan 23, 2018',
        },
      ],
      heading4: 'ads',
      adv:
        'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
      heading5: 'Popular Posts',
    },
    post: '',
    image:
      'https://f2d44896feb7590bd54d-e0950d08dff9458d7b7fab9e628a3499.ssl.cf2.rackcdn.com/3a5feab5aac01b01e88235c75d458c8b.jpeg',
    tags: [
      'Tech',
      'IOS',
      'Computer',
      'IOS',
      'Computer',
      'Tech',
      'Tech',
      'IOS',
      'Computer',
      'IOS',
      'Computer',
      'IOS',
      'Computer',
      'IOS',
      'Computer',
    ],
    newComment: ['Hello I like this article, best wishes', 'Hello everybody'],
    comment: '',
    username: '',
    email: '',
    nextPrev: '',
    loading: false,
  };


  componentDidMount = () => {
    this.setState({ loading: true });
    const intervalId = setInterval(this.switchLoading(), 1000);
    setTimeout(() => {
      this.setState({ loading: false }, () => {
        clearInterval(intervalId);
      });
    }, 2000);
    this.getData(this.props);
  };

  componentWillReceiveProps(props) {
    this.getData(props);
  }

  switchLoading = () => {
    const { loading } = this.state;
    this.setState({ loading: !loading });
  }

getData = (props) => {
  this.switchLoading();
  const intervalId = setInterval(this.switchLoading(), 1000);
  setTimeout(() => {
    this.setState({ loading: false }, () => {
      clearInterval(intervalId);
    });
  }, 1000);
  const {
    match: {
      params: { seoName, category },
    },
  } = props;

  axios(`/api/getPost?seo_name=${seoName}`).then((res) => {
    const {
      data,
    } = res;
    const { data: { category: { name } } } = data;
    if (name === category) {
      this.setState({ post: data.data, nextPrev: data });
    } else {
      const { history } = this.props;
      history.push('/error');
    }
  }).catch((error) => {
    const { response: { data: { status: { code } } } } = error;
    if (code === 403) {
      const { history } = this.props;
      history.push('/error');
    }
  });
}

  onChange = ({ target: { name, value } }) => {
    this.setState({ [name]: value });
  };

  addComment = () => {
    const {
      comment, username, email, newComment,
    } = this.state;
    newComment.push(comment);
    this.setState({ newComment });
  };

  render() {
    const {
      right,
      post,
      image,
      tags,
      comment,
      newComment,
      username,
      email,
      nextPrev,
      loading,
    } = this.state;
    return (

      <div className="newsPage">
        {loading ? <Loading /> : null}
        <div className="container">
          <div className="NewsContainer">
            <div className="left-section">
              <Article postDetails={post} />
              <img className="adv" src={image} alt="adv" />
              <ArticleDetails postDetails={post} />
              <ArticleTags tags={tags} />
              <Auther details={post.auther} />
              <Next data={nextPrev} />
              <Heading desc="Leave Comment" />
              <Comment
                users={post.auther}
                onChange={this.onChange}
                value={comment}
                onClick={this.addComment}
              />
              <hr className="seperate" />
              <Comments
                users={post.auther}
                user={post.auther}
                newComment={newComment}
                name={username}
                email={email}
              />
            </div>
            <RightSection data={right} />
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(NewsPageContainer);
