/* eslint-disable no-nested-ternary */
/* eslint-disable no-plusplus */
/* eslint-disable react/prop-types */
import React, { Component, Fragment } from 'react';
import './style.css';
import uuid from 'uuid';
import RemoveTags from 'striptags';
import { Link } from 'react-router-dom';
import axios from 'axios';
import Pagination from 'rc-pagination';
import Heading from '../../../../common/Heading';
import RightSection from '../../../../common/RightSection';
import Loading from '../../../../common/Loading';
import Title from '../../../../common/Title';
import './pagination.css';

export default class BottomSection extends Component {
  state = {
    right: {
      image:
        'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
      heading1: 'Keep In Touch',
      video:
        'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
      heading2: 'Videos',
      heading3: 'Popular Posts',
      posts: [
        {
          image:
            'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
          title:
            'We decided to use this room, on the second floor and overlooking',
          date: 'Jan 23, 2018',
        },
        {
          image:
            'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
          title:
            'We decided to use this room, on the second floor and overlooking',
          date: 'Jan 23, 2018',
        },
        {
          image:
            'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
          title:
            'We decided to use this room, on the second floor and overlooking',
          date: 'Jan 23, 2018',
        },
        {
          image:
            'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
          title:
            'We decided to use this room, on the second floor and overlooking',
          date: 'Jan 23, 2018',
        },
        {
          image:
            'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
          title:
            'We decided to use this room, on the second floor and overlooking',
          date: 'Jan 23, 2018',
        },
      ],
      heading4: 'ads',
      adv:
        'https://images.pexels.com/photos/6335/man-coffee-cup-pen.jpg?auto=compress&cs=tinysrgb&h=650&w=940',
      heading5: 'Popular Posts',
    },
    currentPage: '',
    loading: false,
    allNews: [],
  };

  static getDerivedStateFromProps(nextProps, prevState) {
    const { data } = nextProps;
    const { posts, name } = data;
    const {
      last_page: lastPage,
      from: firstPage,
      current_page: currentPage,
      total,
    } = posts;
    const allNews = posts.data;
    if (prevState.name !== name) {
      return {
        allNews,
        lastPage,
        firstPage,
        currentPage,
        name,
        total,
      };
    }
    return null;
  }

  changeData = (current, pagesize) => {
    const { name } = this.state;
    axios(
      `/api/getCategoryPosts?seo_name=${name}&page=${current}&per_page=${pagesize}`,
    ).then((result) => {
      const {
        data: {
          data: { posts },
        },
      } = result;
      const allNews = posts.data;

      this.setState(() => ({ allNews }), () => window.scrollTo(0, 0));
    });
  };


  render() {
    const { right, allNews, total } = this.state;
    return (
      allNews.length ? (
        <div className="category-bottom-section">
          <div>
            <div className="bottom-left">
              <Heading desc="All News" />
              {allNews
                ? allNews.map(element => (
                  <Fragment key={uuid()}>
                    <div className="post">
                      <div className="post-img">
                        <img
                          src={`/storage/${element.header_photo_link}`}
                          alt=""
                        />
                      </div>
                      <div className="post-details">
                        <div className="category-name-spec">
                          <Title title={element.category.name} />
                        </div>
                        <Link
                          to={`/news/${element.category.name}/${
                            element.seo_name
                          }`}
                        >
                          {element.name}
                        </Link>
                        <span className="category-post-details">
                          {RemoveTags(element.details)}
                        </span>
                        <span className="category-post-date">
                          {element.created_at}
                        </span>
                      </div>
                    </div>
                  </Fragment>
                )) : null
            }
            </div>

            <div className="category-pagingg">
              <Pagination
                hideOnSinglePage
                total={total}
                defaultPageSize={7}
                onChange={(current, pageSize) => this.changeData(current, pageSize)
              }
              />
            </div>
          </div>
          <RightSection data={right} />
        </div>
      ) : null
    );
  }
}
